.services {
  &__wrapper {
    background-image: url(https://res.cloudinary.com/de2ofvts5/image/upload/f_auto/v1600534234/altr/kv-page-lain_cplgej.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
  .heading {
    max-width: 225px;
    @media only screen and (min-width: 768px) {
      align-self: flex-end;
      position: fixed;
      bottom: 0;
    }
    h1 {
      font-family: "GTAmerica-ExtendedBlack";
      font-size: 3.25rem;
      letter-spacing: 0.6rem;
      text-transform: uppercase;
      @media only screen and (min-width: 768px) {
        overflow-wrap: break-word;
        word-wrap: break-word;
        -webkit-hyphens: auto;
        -ms-hyphens: auto;
        -moz-hyphens: auto;
        hyphens: auto;
      }
    }
  }
  ul {
    @media only screen and (min-width: 768px) {
      padding-left: 325px;
      margin-top: 3rem;
    }
    li {
      max-width: 638px;
      font-family: "GTAmerica-ExtendedMedium";
      font-size: 1rem;
      margin-bottom: 2.5rem;
      &.active {
        position: relative;
        font-family: "GTAmerica-ExtendedBlack";
        &::before {
          content: url("../../images/arrow.svg");
          position: absolute;
          right: 100%;
          padding-right: 2rem;
        }
      }
      a {
        color: #fff;
        cursor: pointer;
      }
      .content-subitem {
        display: none;
        border-top: 2px solid #fff;
        padding-top: 1.5rem;
        p {
          font-family: "GTAmerica-Extended";
        }
      }
      @media only screen and (max-width: 768px) {
        &:last-child {
          padding-bottom: 5rem;
        }
      }
    }
  }
}
